import React from 'react';
import { ReactNode } from 'react';
import { styled } from "@stitches/react";
import{ TopPage, TopPageProps } from './stories/pages/TopPage/index';
import{ TimeChangeButtonInterface } from './stories/molecules/PanelerPanel/index';

type PanelerProps = {
  panelerImage?: ReactNode;
  panelerName: string;
  ownTimeInSec: number;
  originOwnTimeInSec: number;
  isTalkOn: boolean;
  timeChangeButtons: Array<TimeChangeButtonInterface>;
};

type AdminProps = {
  eventName: string;
  eventTimeInSec: number;
};

type TalkOnProps = {
  panelerId: number;
  timer: number;
};

const getDefaultPanelerProps = (panelerName: string, ownTimeInSec: number, originOwnTimeInSec: number) => {
  return {
    panelerName,
    ownTimeInSec,
    originOwnTimeInSec,
    isTalkOn: false,
    timeChangeButtons: [
      {
        label: '5分\n追加',
        type: 'add',
        payload: { time: 5 * 60 }
      },
    ],
  } as PanelerProps;
};

const getImageSrcFromFileAsync = async (file: File) => {

  return new Promise<string>(resolve => {

    const img = document.createElement('img');
    img.alt = 'Avator';

    const reader = new FileReader();
    reader.onload = function(event: any) {
      resolve(event.target.result);
    };
    reader.readAsDataURL(file);

  });

};

const updatePropsInList = (propsList: Array<PanelerProps>, index: number, prop: { [k: string]: any }) => {

  propsList[index] = {
    ...propsList[index],
    ...prop
  };
  return [...propsList];

};

const updateIsTalkOn = (propsList: Array<PanelerProps>, id?: number) => {

  return propsList.map((props: PanelerProps, _id: number) => {
    props.isTalkOn = id === _id;
    return props;
  })

};

const _App: React.FC = ({
  ...props
}) => {

  // State
  const [adminProps, setAdminProps] = React.useState<AdminProps>({
    eventName: 'テストイベント',
    eventTimeInSec: 60 * 60,
  });
  const [panelerPropsList, setPanelerPropsList] = React.useState<Array<PanelerProps>>([
    getDefaultPanelerProps('Aさん', adminProps.eventTimeInSec, adminProps.eventTimeInSec),
    getDefaultPanelerProps('Bさん', adminProps.eventTimeInSec, adminProps.eventTimeInSec),
    getDefaultPanelerProps('Cさん', adminProps.eventTimeInSec, adminProps.eventTimeInSec),
    getDefaultPanelerProps('Dさん', adminProps.eventTimeInSec, adminProps.eventTimeInSec)
  ]);
  const [talkOnProps, setTalkOnProps] = React.useState<TalkOnProps | null>(null);
  const [eventTimer, setEventTimer] = React.useState<number | null>(null);

  // Ref
  const eventTimerRef = React.useRef<number | null>(eventTimer);
  const originEventTimeRef = React.useRef<number>(adminProps.eventTimeInSec);
  const talkOnPanelerOwnTimeRef = React.useRef<number | null>(null);
  const adminPropsRef = React.useRef<AdminProps>(adminProps);
  const panelerPropsListRef = React.useRef<Array<PanelerProps>>(panelerPropsList);
  const serialRef = React.useRef<any>(null);

  // Callback
  const releaseTalkOn = React.useCallback((panelerId: number) => {

    if (talkOnProps === null) return;
    window.clearInterval(talkOnProps.timer);
    setTalkOnProps(null);
    talkOnPanelerOwnTimeRef.current = null;

  }, [talkOnProps]);

  const takeTalkOn = React.useCallback((panelerId: number, ownTimeInSec: number) => {

    releaseTalkOn(panelerId);
    setTalkOnProps({
      panelerId,
      timer: window.setInterval(() => {

        const panelerPropsList = panelerPropsListRef.current;
        const ownTimeInSec = talkOnPanelerOwnTimeRef.current! - 1;
        setPanelerPropsList(updatePropsInList(panelerPropsList, panelerId, { ownTimeInSec }));

      }, 1000),
    });
    talkOnPanelerOwnTimeRef.current = ownTimeInSec;

  }, [releaseTalkOn]);

  const onChangeEventName = React.useCallback((eventName: string) => {
    setAdminProps({ ...adminProps, eventName });
  }, [adminProps]);

  const onChangeEventTime = React.useCallback((eventTimeInSec: number) => {
    setAdminProps({ ...adminProps, eventTimeInSec });
  }, [adminProps]);

  const onClickStartButton = React.useCallback((event: any) => {

    if (eventTimer === null) {

      setEventTimer(window.setInterval(() => {

        const adminProps = adminPropsRef.current;
        const eventTimeInSec = adminProps.eventTimeInSec - 1;
        setAdminProps({ ...adminProps, eventTimeInSec });

      }, 1000));

    }

  }, [eventTimer]);

  const onChangeProfileImage = React.useCallback(async (id: number, files: FileList) => {

    const panelerImage = (
      <img
        style={{
          objectFit: 'cover',
          width: '100%',
          height: '100%',
        }}
        alt="Paneler"
        src={(await getImageSrcFromFileAsync(files[0]))}
      />
    );
    setPanelerPropsList(updatePropsInList(panelerPropsList, id, { panelerImage }));

  }, [panelerPropsList]);

  const onChangePanelerName = React.useCallback((id: number, panelerName: string) => {
    setPanelerPropsList(updatePropsInList(panelerPropsList, id, { panelerName }));
  }, [panelerPropsList]);

  const onChangeOwnTime = React.useCallback((id: number, ownTimeInSec: number, originOwnTimeInSec: number) => {
    setPanelerPropsList(updatePropsInList(panelerPropsList, id, { ownTimeInSec, originOwnTimeInSec }));
  }, [panelerPropsList]);

  const onClickTalkOnButton = React.useCallback((id: number) => {

    setPanelerPropsList(updateIsTalkOn(panelerPropsList));
    releaseTalkOn(id);

  }, [panelerPropsList, releaseTalkOn]);

  const onClickTalkOffButton = React.useCallback((id: number) => {

    if (eventTimerRef.current === null) return;
    if (panelerPropsList.findIndex(props => props.isTalkOn) > -1) return;
    setPanelerPropsList(updateIsTalkOn(panelerPropsList, id));
    takeTalkOn(id, panelerPropsList[id].ownTimeInSec);

  }, [panelerPropsList, takeTalkOn]);

  const onClickTimeChangeButton = React.useCallback((id: number, seconds: number) => {

    const eventTimeInSec = adminProps.eventTimeInSec;
    const originOwnTimeInSec = panelerPropsList[id].originOwnTimeInSec;
    const ownTimeInSec = Math.min(originOwnTimeInSec, Math.min(eventTimeInSec, panelerPropsList[id].ownTimeInSec + seconds));
    setPanelerPropsList(updatePropsInList(panelerPropsList, id, { ownTimeInSec }));

  }, [panelerPropsList, adminProps]);

  const onClickAddTimeChangeButton = React.useCallback((id: number) => {

    const label = prompt('名前を入力してください') ?? '';
    if (label.length === 0) return;
    const time = prompt('秒を入力してください', String(5 * 60));
    if (time !== null) {
      const timeInt = parseInt(time);
      if (!isNaN(timeInt)) {

        const timeChangeButtons = [...panelerPropsList[id].timeChangeButtons];
        timeChangeButtons.push({
          num: timeChangeButtons.length,
          label,
          type: 'add',
          payload: { time: timeInt }
        } as TimeChangeButtonInterface);
        setPanelerPropsList(updatePropsInList(panelerPropsList, id, { timeChangeButtons }));
        
      }
    }

  }, [panelerPropsList]);

  const onClickTrashTimeChangeButton = React.useCallback((id: number, _i: number) => {

    const timeChangeButtons = panelerPropsList[id].timeChangeButtons.filter((_, i) => i !== _i);
    setPanelerPropsList(updatePropsInList(panelerPropsList, id, { timeChangeButtons }));

  }, [panelerPropsList]);

  const toggleTalkOn = React.useCallback((id: number) => {

    // 非同期にループされたところから呼ばれるので、useStateされた値は期待通りではない
    // useRefで現在の最新値を使って各メソッドで対応
    if (eventTimerRef.current === null) return;
    const panelerPropsList = panelerPropsListRef.current;
    const currentPanelerId = panelerPropsList.findIndex(props => props.isTalkOn);
    if (currentPanelerId === id) {

      setPanelerPropsList(updateIsTalkOn(panelerPropsList));
      releaseTalkOn(id);

    } else if (currentPanelerId === -1) {

      setPanelerPropsList(updateIsTalkOn(panelerPropsList, id));
      takeTalkOn(id, panelerPropsList[id].ownTimeInSec);

    }

  }, [takeTalkOn, releaseTalkOn]);

  const readSerial = React.useCallback(async (port: any) => {

    while (port.readable) {
      const reader = port.readable.getReader();
      try {
        while (true) {
          const { value, done } = await reader.read();
          if (done) {
            break;
          }
          switch (value[0]) {
            case 8:
              toggleTalkOn(0);
              break;
            case 4:
              toggleTalkOn(1);
              break;
            case 2:
              toggleTalkOn(2);
              break;
            case 1:
            default:
              toggleTalkOn(3);
              break;
          }
        }
      } catch(error) {
        console.error(error);
        break;
      } finally {
        reader.releaseLock();
      }
    }

  }, [toggleTalkOn]);

  const openSerialPort = React.useCallback(async () => {

    if ("serial" in navigator) {

      const serial = (navigator as any).serial;
      const port = await serial.requestPort().catch(console.log) ?? null;
      if (port === null) return;
      port.onconnect = (event: any) => {
        console.log('connected', event);
      };
      port.ondisconnect = (event: any) => {
        console.log('disconnected', event);
      };
      serialRef.current = port;
      await port.open({ baudRate: 115200 });
      await readSerial(port);

    } else {
      alert('WebSerialがサポートされていないブラウザです');
    }
    serialRef.current = null;

  }, [readSerial]);

  const onClickBodyButton = React.useCallback((event: any) => {

    if (serialRef.current === null) {
      openSerialPort();
    }

  }, [openSerialPort]);

  // Effect
  React.useEffect(() => {
    adminPropsRef.current = adminProps;
  }, [adminProps, eventTimer]);

  React.useEffect(() => {

    if (eventTimer === null) {
      originEventTimeRef.current = adminProps.eventTimeInSec;
    }

  }, [adminProps, eventTimer]);

  React.useEffect(() => {

    if (talkOnProps === null) return;
    const { panelerId } = talkOnProps;
    const panelerProps = panelerPropsList[panelerId];
    if (panelerProps.isTalkOn) {
      talkOnPanelerOwnTimeRef.current = panelerProps.ownTimeInSec;
    } else {
      releaseTalkOn(panelerId);
    }

  }, [panelerPropsList, talkOnProps, releaseTalkOn]);

  React.useEffect(() => {
    panelerPropsListRef.current = panelerPropsList;
  }, [panelerPropsList]);

  React.useEffect(() => {
    eventTimerRef.current = eventTimer;
  }, [eventTimer]);

  // Component
  const topPageProps: TopPageProps = React.useMemo(() => {

    const panelerPanelProps = panelerPropsList.map((props: PanelerProps, id: number) => {

      return {
        ...props,
        barHeight: 16,
        barColor: 'red',
        progressColor: 'blue',
        onChangeProfileImage: (files: FileList) => onChangeProfileImage(id, files),
        onChangePanelerName: (name: string) => onChangePanelerName(id, name),
        onChangeOwnTime: (ownTimeInSec: number, originOwnTimeInSec: number) => onChangeOwnTime(id, ownTimeInSec, originOwnTimeInSec),
        onClickTalkOnButton: () => onClickTalkOnButton(id),
        onClickTalkOffButton: () => onClickTalkOffButton(id),
        onClickTimeChangeButton: (seconds: number) => onClickTimeChangeButton(id, seconds),
        onClickTrashTimeChangeButton: (i: number) => onClickTrashTimeChangeButton(id, i),
        onClickAddTimeChangeButton: () => onClickAddTimeChangeButton(id),
      };

    });
    return {
      ...adminProps,
      isStarted: eventTimer !== null,
      panelerPanelProps,
      onClickStartButton,
      onChangeEventName,
      onChangeEventTime,
    };

  }, [adminProps, panelerPropsList, eventTimer, onChangeEventName, onChangeEventTime, onChangePanelerName, onChangeOwnTime, onClickTalkOnButton, onClickTalkOffButton, onClickTimeChangeButton, onClickTrashTimeChangeButton, onClickAddTimeChangeButton, onChangeProfileImage, onClickStartButton]);

  return (
    <div
      {...props}
      onClick={onClickBodyButton}
    >
      <TopPage
        {...topPageProps}
      />
    </div>
  );

};

const App = styled(_App, {
  padding: 24,
  height: 'calc(100vh - 48px)',
  display: 'grid',
});

export default App;

